.free {
	background-image: url('../img/bg-free.jpg');
	background-size: cover;
	padding-top: 275px;
	padding-bottom: 80px;
	color: $white;
	background-position: 50% 50%;
	overflow: hidden;
	@include r($tablet) {
		background-image: url('../img/bg-free-m.jpg');
		padding-bottom: 40px; }
	@include r($mobile) {
		background-image: url('../img/bg-free-m2.jpg');
		padding-top: 70px; }
	h2 {
		font-size: 55px;
		margin-bottom: 35px;
		@include r($desktop) {
			font-size: 37.5px;
			margin-bottom: 25px; }
		@include r($tablet) {
			font-size: 45px;
			margin-bottom: 55px;
			br {
				display: none; } }
		@include r($mobile) {
			font-size: 25px;
			text-align: center;
			margin-bottom: 35px; } }
	p {
		font-size: 21.5px;
		line-height: 1.4;
		margin-bottom: 50px;
		@include r($desktop) {
			font-size: 14.5px;
			margin-bottom: 30px; }
		@include r($tablet) {
			margin-bottom: 30px; }
		@include r($mobile) {
			font-size: 13.5px;
			margin-bottom: 20px;
			text-align: center;
			br {
				display: none; } } }
	form {
		input {
			width: 430px;
			margin-right: 20px;
			margin-bottom: 50px;
			&::placeholder {
				color: $white;
				@extend .roboto;
				font-weight: 300; }
			@include r($desktop) {
				height: 45px;
				font-size: 12.5px;
				width: 300px;
				margin-right: 13px;
				margin-bottom: 30px; }
			@include r($mobile) {
				width: 100%; } }
		button {
			width: 430px;
			@include r($desktop) {
				width: 300px;
				height: 55px; }
			@include r($tablet) {
				width: 345px;
				height: 80px;
				font-size: 19.5px; }
			@include r($mobile) {
				width: 100%;
				height: 70px;
				i {
					margin-right: 15px; } } }
		> span {
			@extend .roboto;
			font-size: 20px;
			margin-top: 50px;
			display: block;
			@include r($tablet) {
				font-weight: 300; }
			@include r($mobile) {
				font-size: 13.5px;
				margin-top: 40px;
				text-align: center;
				br {
					display: none; } } }
		@include r($tablet) {
			.flex:nth-of-type(2) {
				flex-direction: column; }
			.social__info {
				margin-left: 0;
				margin-top: 55px; } }
		@include r($mobile) {
			.flex {
				flex-direction: column; }
			.social__info {
				margin-top: 50px;
				p {
					font-size: 16px; }
				a {
					justify-content: center;
					margin-bottom: 25px; } } } } }
