.btn-primary {
  background-color: #2849ae;
  position: relative;
}

.btn-primary:before {
  content: '';
  background-image: linear-gradient(94deg, #2335a7, #4ae0e2 97%);
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: all 0.3s;
}

.btn-primary:hover:before {
  opacity: 1;
}

.btn-secondary {
  background-color: transparent;
  border: 3px solid #ffffff;
  position: relative;
}

