.subscribe {
  background-image: url('../img/bg-subscribe.jpg');
  background-size: cover;
  padding-top: 100px;
  padding-bottom: 85px;
  position: relative;
  overflow: hidden;
  @include r($tablet) {
    padding-top: 60px;
    padding-bottom: 50px; }
  @include r($mobile) {
    padding-top: 40px;
    padding-bottom: 50px; }
  &:before {
    content: '';
    background-image: url('../img/phone.png');
    display: block;
    width: 370px;
    height: 470px;
    position: absolute;
    right: 100px;
    top: -68px;
    @include r(1679) {
      right: 20px;
      top: -81px; }
    @include r(1439) {
      width: 250px;
      height: 320px;
      background-size: cover;
      top: 58px; }
    @include r(1199) {
      content: none; }
    @include r($tablet) {
      content: '';
      width: 330px;
      height: 420px;
      right: 2px;
      top: 90px; }
    @include r($mobile) {
      content: none; } }
  h2 {
    font-size: 35px;
    color: #292929;
    margin-bottom: 50px;
    @include r(1679) {
      font-size: 30px; }
    @include r(1439) {
      font-size: 24px; }
    @include r($tablet) {
      font-size: 25px;
      margin-bottom: 40px; }
    @include r($mobile) {
      font-size: 18px;
      margin-bottom: 30px;
      text-align: center; } }
  form {
    display: flex;
    @include r($tablet) {
      flex-direction: column;
      margin-bottom: 50px; }
    input {
      width: 430px;
      margin-right: 20px;
      @include r($tablet) {
        width: 345px; }
      @include r($mobile) {
        width: 100%; } }
    button {
      width: 280px;
      height: 65px;
      @include r($tablet) {
        width: 345px; }
      @include r($mobile) {
        width: 100%; } } }
  .social__info {
    position: relative;
    bottom: 20px;
    margin-left: 70px;
    @include r(1800) {
      margin-left: 20px; }
    @include r(1439) {
      margin-left: 10px; }
    @include r($tablet) {
      bottom: 0; }
    a {
      color: #292929; }
    @include r($mobile) {
      p {
        font-size: 16px;
        text-align: center; }
      .flex {
        flex-wrap: wrap;
        justify-content: center; }
      a {
        justify-content: center;
        margin-bottom: 20px;
        font-size: 16px;
        margin-right: 10px;
        i {
          font-size: 26px; }
        &:nth-of-type(1), &:nth-of-type(2) {} } } } }
